@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@200;300;500;800&family=Poppins:wght@400;500;600&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;  
}

:root{
    --color-bg: #171734ee;
    --color-bg-tekno: #241c30;
    --color-bg-defaults: #e1e1e1;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-success: #79e4a7;
    --color-primary-variant: rgba(255, 255, 255,0.4);
    --color-secondary: rgb(65, 65, 65);
    --color-whtie: white !important;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 80%;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    /* font-family: 'Kanit', sans-serif; */
    width:100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: var(--color-bg);
    color: var(--color-whtie) !important;
    line-height: 1.7;
    background-image: url(../src/assets/Texture-background-png\,-Picture-405085-honeycomb-background-.png);
    background-blend-mode: hue;
}

/* ======== GENERAL CSS ======== */
.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}


h1, 
h2, 
h3, 
h4,
h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top: 3rem;
    height: 100vh;
}

section > h2, 
section > h5 {
    columns: var(--color-light);
}

section > h2 {
    columns: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-light);
}

a{
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-whtie);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    cursor: pointer;
    border-radius: 17px;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background: var(--color-whtie);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}
.btn-secondary{
    background: var(--color-success);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ========== Medium Device =========== */
@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-md);
    }

}

/* ========== Small Device =========== */
@media screen and (max-width: 600px){
    .container{
        width: var(--container-width-sm);
    }

    section > h2{
        margin-top: 2rem;
    }
    section{
        height: 160vh;
    }
    section{
        margin-top: 0rem;
        padding-top: 0rem !important
    }
}